<template>
    <yd-layout>
        <div class="sealAgent">
            <div class="line" v-if="submitActivity=='officialSeal_submitApplication'" :class="{lineACtive:lineNum==1}" @click="clickInfo(1)">返回至申请人</div>
            <div class="line" v-if="secretaryActivity=='officialSeal_secretaryApproval'" :class="{lineACtive:lineNum==2}" @click="clickInfo(2)">送秘书预审{{secretaryName}}</div>
            <!-- <div class="line" v-if="assistActivity=='officialSeal_assistApproval'" @click="clickInfo(3);">
                <div :class="{lineACtive2:lineNum==3}">送协助审查</div>
                <div>{{selecter}}</div>
            </div> -->
            <div class="line" v-if="finishActivity=='finishActivity'" :class="{lineACtive:lineNum==4}" @click="clickInfo(4);">完成受理</div>
            <div class="finishClass" v-if="lineNum==4">
                <yd-cell-item>
                    <span slot="left" class="qyTime">签章预约时间：</span>
                    <yd-datetime type="date" v-model="datetime" slot="right"></yd-datetime>
                </yd-cell-item>
                <div class="finSugg">
                    <div class="qyTime">受理意见：</div>
                    <trsTextarea placeholder="请您输入意见" v-model="opinion"></trsTextarea>
                </div>
            </div>
        </div>
        <yd-popup v-model="statusPopup" position="bottom" height="4rem">
            <div class="confirmBtn" slot="top">
                <div class="leftBtn" v-on:click="statusPopup = false">取消</div>
                <div class="rightBtn" v-on:click="statusPopup = false;leaderIDs=itemId">确定</div>
            </div>
            <div class="selectStatus">
                <ul class="ul">
                    <li class="li" v-for="(item, index) in secretariesList" :key="index" v-bind:class="{active:statusRadio==index}" v-on:click="statusRadio=index;itemId=item.operatorid;secretaryName='（' + item.empname + '）'">{{item.empname}}
                    </li>
                </ul>
            </div>
        </yd-popup>
        <yd-tabbar slot="tabbar" v-show="statusPopup==false">
            <div class="back" @click="$router.go(-1)">返回</div>
            <div class="agree" @click="sendBtn()">确认发送</div>
        </yd-tabbar>
    </yd-layout>
</template>
<script>
import { mapGetters, mapMutations } from 'vuex';
export default {
    name: 'incAccept',
    data() {
        return {
            lineNum: -1,
            statusPopup: false,
            statusRadio: -1,
            nextBtn: {},
            secretariesList: [],
            activityID: '',
            leaderIDs: '',
            secretaryName: '',
            finishShow: false,
            opinion: '',
            submitIds: '',
            submitActivity: '',
            secretaryActivity: '',
            assistActivity: '',
            finishActivity: '',
            datetime: '',
            itemId: '',
            selecter: '',
            assistLeaderId: [],
            ext: ''
        }
    },
    mounted() {
        let vm = this;
        vm.getData();
        vm.getSecretary();
        // vm.select();
        // vm.getAssist();
        vm.datetime = vm.formatDate(new Date(), 'yyyy-MM-dd');
    },
    computed: {
        ...mapGetters(['getUserInfo', 'getSealInfo', 'getSearchInfo'])
    },
    methods: {
        ...mapMutations([
            'setSealInfo',
            'setSearchInfo'
        ]),
        getData() {
            let vm = this;
            vm.nextBtn = vm.getSealInfo.jsonData.nextBtn;
            for (let key in vm.nextBtn) {
                if (key == 'officialSeal_submitApplication') {
                    vm.submitIds = vm.nextBtn.officialSeal_submitApplication[0];
                    vm.submitActivity = key;
                } else if (key == 'officialSeal_secretaryApproval') {
                    vm.secretaryActivity = key;
                } else if (key == 'officialSeal_assistApproval') {
                    vm.assistActivity = key;
                } else if (key == 'finishActivity') {
                    vm.finishActivity = key;
                }
            }
        },
        // select() {
        //     let vm = this;
        //     vm.lineNum = vm.getSealInfo.lineNum;
        //     if (vm.lineNum == 3) {
        //         vm.activityID = 'officialSeal_assistApproval';
        //     }
        //     vm.getSearchInfo.selectPerson.map((item) => {
        //         vm.selecter += item.empname + '、';
        //         // vm.leaderIDs += item.empcode + ',';
        //     });
        //     vm.selecter = vm.selecter.substring(0, vm.selecter.length - 1);
        //     // vm.leaderIDs = vm.leaderIDs.substring(0, vm.leaderIDs.length - 1);
        // },
        // 获取秘书
        getSecretary() {
            var vm = this;
            vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/handlerPerson', {
                'type': 'officialSeal',
                'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                'queryType': 7
            }, function(res) {
                vm.secretariesList = res.jsonData;
            });
        },
        // 获取协助人员
        // getAssist() {
        //     var vm = this;
        //     vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/handlerPerson', {
        //         'type': 'officialSeal',
        //         'personID': vm.getSearchInfo.selUserId.toString(),
        //         'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
        //         'queryType': 30
        //     }, function(res) {
        //         res.jsonData.map((item) => {
        //             vm.assistLeaderId.push(item.operatorid);
        //         })
        //     });
        // },
        clickInfo(num) {
            let vm = this;
            if (num == 1) {
                vm.lineNum = 1;
                vm.leaderIDs = vm.submitIds;
                vm.activityID = vm.submitActivity;
                vm.secretaryName = '';
                vm.statusRadio = -1;
            } else if (num == 2) {
                vm.lineNum = 2;
                vm.leaderIDs = '';
                vm.activityID = vm.secretaryActivity;
                vm.statusPopup = true;
                // vm.activityID = '';
            } else if (num == 3) {
                vm.lineNum = 3;
                vm.setSealInfo({
                    lineNum: 3
                });
                vm.leaderIDs = '';
                vm.secretaryName = '';
                vm.statusRadio = -1;
                vm.$router.push('/sealSelectP');
                vm.activityID = vm.assistActivity;
            } else if (num == 4) {
                vm.lineNum = 4;
                vm.finishShow = true;
                vm.secretaryName = '';
                vm.statusRadio = -1;
                vm.leaderIDs = '';
                vm.activityID = vm.finishActivity;
            }
        },
        sendBtn() {
            let vm = this;
            if (vm.lineNum == -1) {
                vm.$dialog.toast({
                    mes: '请选择发送选项',
                    timeout: 2000
                });
            } else if (vm.lineNum == 2 && vm.leaderIDs == '') {
                vm.$dialog.toast({
                    mes: '请选择秘书',
                    timeout: 2000
                });
            } else if (vm.lineNum == 3 && vm.assistLeaderId.toString() == '') {
                vm.$dialog.toast({
                    mes: '请选择协助审查人员',
                    timeout: 2000
                });
            } else {
                if (vm.lineNum == 4) {
                    vm.ext = '{"ordertime":"0","orderdate":"' + vm.datetime + '"}';
                } else if (vm.lineNum == 3) {
                    vm.leaderIDs = vm.assistLeaderId.toString();
                } else {
                    vm.opinion = '';
                }
                vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/doOANext', {
                    'type': 'officialSeal',
                    'workItemID': vm.$route.params.workId,
                    'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                    'activityID': vm.activityID,
                    'leaderIDs': vm.leaderIDs,
                    'fuyan': '',
                    'opinion': vm.opinion,
                    'workTitle': vm.getSealInfo.jsonData.firstorgname + vm.getSealInfo.jsonData.applicantname + '收入证明申请',
                    'ext': vm.ext
                }, function(res) {
                    if (res.isSuccess == 'true') {
                        vm.setSealInfo({
                            applyoffsname: '',
                            approver: [],
                            approveType: '',
                            applyoffsid: '',
                            jsonData: '',
                            fuyan: '',
                            sealArray: [],
                            fileNum: 0,
                            lineNum: -1
                        })
                        vm.setSearchInfo({
                            selectPerson: [],
                            selEmpCode: [],
                            selUserId: []
                        })
                        vm.$dialog.toast({
                            mes: '发送成功',
                            timeout: 2000,
                            callback: () => {
                                vm.$router.push('/handle');
                            }
                        });
                    }
                });
            }
        }
    }
}
</script>
<style lang="less" scoped>
@import 'commonSeal.less';

.sealAgent {
    min-height: 100%;
    background-color: #fff;

    .line {
        min-height: 1.1rem;
        line-height: 1.1rem;
        font-size: .32rem;
        color: #333;
        padding: 0 .45rem;
        border-bottom: 1px solid #eaeaea;
    }

    .lineACtive {
        background: url(../../../static/images/workCenter/20.png) no-repeat;
        background-position: 6.5rem center;
    }

    .lineACtive2 {
        background: url(../../../static/images/workCenter/20.png) no-repeat;
        background-position: 6rem center;
    }

    .finishClass {
        padding: 0 .45rem;
        font-size: .32rem;
        margin-top: .1rem;

        .yd-cell-item {
            padding-left: 0;
        }

        .qyTime {
            font-size: .32rem;
            color: #333;
        }

        .yd-datetime-input {
            font-size: .32rem;
            color: #333;
        }

        .finSugg {
            display: flex;
        }

        .trsTextarea {
            min-height: 2rem;
            -webkit-box-flex: 1;
            -webkit-flex: 1;
            -ms-flex: 1;
            flex: 1;
        }
    }
}
.yd-cell-item:not(:last-child):after {
    background-image: none;
}
</style>


